
.header {
  color: #fff;
  background: rgba(7,17,27,0.5);
  position: relative;
  overflow: hidden;
}
.header .content_wrapper {
  padding: 24px 12px 18px 24px;
  font-size: 0;
  position: relative;
}
.header .content_wrapper .avatar {
  display: inline-block;
}
.header .content_wrapper .avatar img {
  border-radius: 2px;
}
.header .content_wrapper .content {
  display: inline-block;
  margin-left: 16px;
}
.header .content_wrapper .content .title {
  margin: 2px 0 8px 0;
}
.header .content_wrapper .content .title .brand {
  display: inline-block;
  width: 30px;
  height: 18px;
  vertical-align: top;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content_wrapper .content .title .name {
  margin-left: 6px;
  line-height: 18px;
  font-size: 16px;
  font-weight: bold;
}
.header .content_wrapper .content .description {
  line-height: 12px;
  margin-bottom: 10px;
  font-size: 12px;
  font-weight: 200;
}
.header .content_wrapper .content .support .icon {
  margin-right: 2px;
}
.header .content_wrapper .content .support .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .support .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.header .content_wrapper .content .support .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .support .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.header .content_wrapper .content .support .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .support .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.header .content_wrapper .content .support .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .support .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.header .content_wrapper .content .support .icon.special {
  background-image: url("special_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content_wrapper .content .support .icon.special {
    background-image: url("special_1@3x.png");
}
}
.header .content_wrapper .content .support .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content_wrapper .support_count {
  position: absolute;
  right: 12px;
  bottom: 14px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 14px;
  background: rgba(0,0,0,0.2);
}
.header .content_wrapper .support_count .count {
  font-size: 10px;
}
.header .content_wrapper .support_count .icon-keyboard_arrow_right {
  font-size: 10px;
  margin-left: 2px;
  line-height: 24px;
}
.header .bulletin_wrapper {
  height: 28px;
  line-height: 28px;
  padding: 0 28px 0 28px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  background: rgba(7,17,27,0.2);
}
.header .bulletin_wrapper .bulletin_title {
  display: inline-block;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin_wrapper .bulletin_title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin_wrapper .bulletin_text {
  font-size: 10px;
  margin: 0 4px;
  vertical-align: top;
}
.header .bulletin_wrapper .icon-keyboard_arrow_right {
  position: absolute;
  font-size: 10px;
  right: 12px;
  line-height: 30px;
}
.header .head_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: auto;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
}
.header .detail.fade-enter-active,
.header .detail.fade-leave-active {
  transition: 0.5s;
}
.header .detail.fade-enter {
  opacity: 0;
  background: rgba(7,17,27,0);
}
.header .detail.fade-leave {
  opacity: 1;
  background: rgba(7,17,27,0.8);
}
.header .detail .detail_wrapper {
  min-height: 100%;
  width: 100%;
}
.header .detail .detail_wrapper .detail_main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail_wrapper .detail_main .name {
  line-height: 16px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.header .detail .detail_wrapper .detail_main .star_wrapper {
  margin-top: 16px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail_wrapper .detail_main .title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 80%;
  margin: 30px auto 24px auto;
}
.header .detail .detail_wrapper .detail_main .title .line {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  top: -6px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.header .detail .detail_wrapper .detail_main .title .text {
  padding: 0 12px;
  font-size: 14px;
}
.header .detail .detail_wrapper .detail_main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail_wrapper .detail_main .supports .support_item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail_wrapper .detail_main .supports .support_item:last-child {
  margin-bottom: 0;
}
.header .detail .detail_wrapper .detail_main .supports .support_item .icon {
  width: 16px;
  height: 16px;
  margin-right: 16px;
  background-repeat: no-repeat;
}
.header .detail .detail_wrapper .detail_main .supports .support_item .text {
  line-height: 16px;
  font-size: 12px;
}
.header .detail .detail_wrapper .detail_main .bulletin {
  margin: 0 auto;
  width: 80%;
}
.header .detail .detail_wrapper .detail_main .bulletin .content {
  padding: 0 12px;
  line-height: 24px;
  font-size: 12px;
}
.header .detail .detail_close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  font-size: 32px;
  clear: both;
}


