
.RatingSelect .rating_type {
  padding: 18px 0;
  margin: 0 18px;
  font-size: 0;
  position: relative;
}
.RatingSelect .rating_type:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  width: 100%;
  content: '';
}
.RatingSelect .rating_type .block {
  display: inline-block;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  color: #4d555d;
  font-size: 12px;
  line-height: 24px;
}
.RatingSelect .rating_type .block.active {
  color: #fff;
}
.RatingSelect .rating_type .block .count {
  margin-left: 2px;
  font-size: 8px;
  line-height: 16px;
}
.RatingSelect .rating_type .block.positive {
  background: rgba(0,160,220,0.2);
}
.RatingSelect .rating_type .block.positive.active {
  background: #00a0dc;
}
.RatingSelect .rating_type .block.negative {
  background: rgba(77,85,93,0.2);
}
.RatingSelect .rating_type .block.negative.active {
  background: #4d555d;
}
.RatingSelect .switch {
  padding: 12px 18px;
  line-height: 24px;
  color: #93999f;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.RatingSelect .switch.on .icon-check_circle {
  color: #00c850;
}
.RatingSelect .switch .icon-check_circle {
  margin-right: 4px;
  font-size: 24px;
}
.RatingSelect .switch .text {
  font-size: 12px;
  vertical-align: top;
}
.RatingSelect .no_rating {
  padding: 16px 0;
  font-size: 12px;
  line-height: 24px;
  color: #93999f;
}


