
.foodInfo {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  transition: all 0.3s;
}
.foodInfo.move-enter-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.foodInfo.move-enter,
.foodInfo.move-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.foodInfo .img_header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.foodInfo .img_header .img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.foodInfo .img_header .back {
  position: fixed;
  left: 0;
  top: 10px;
}
.foodInfo .img_header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.foodInfo .content {
  position: relative;
  padding: 18px;
}
.foodInfo .content .title {
  font-size: 14px;
  line-height: 14px;
  margin-bottom: 8px;
  font-weight: 700;
  color: #07111b;
}
.foodInfo .content .extra {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 10px;
  color: #93999f;
}
.foodInfo .content .extra .count {
  margin-right: 12px;
}
.foodInfo .content .price {
  line-height: 24px;
  font-weight: 700px;
}
.foodInfo .content .price .now,
.foodInfo .content .price .old {
  line-height: 24px;
  margin-right: 0px;
}
.foodInfo .content .price .now {
  font-size: 14px;
  font-weight: 700px;
  color: #f01414;
}
.foodInfo .content .price .old {
  font-size: 10px;
  color: #93999f;
  text-decoration: line-through;
}
.foodInfo .caraControl_wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.foodInfo .addCartShop {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  padding: 0 12px;
  text-align: center;
  line-height: 24px;
  height: 24px;
  font-size: 10px;
  color: #fff;
  background: #00a0dc;
  border-radius: 12px;
  box-sizing: border-box;
  transition: all 0.2s;
}
.foodInfo .addCartShop.addCartMove-enter-active {
  opacity: 1;
}
.foodInfo .addCartShop.addCartMove-enter,
.foodInfo .addCartShop.addCartMove-leave-to {
  opacity: 0;
}
.foodInfo .info {
  padding: 18px;
}
.foodInfo .info .title {
  line-height: 14px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.foodInfo .info .text {
  color: #4d555d;
  line-height: 24px;
  font-size: 12px;
  font-weight: 200;
  padding: 0 8px;
}
.foodInfo .rating {
  padding-top: 18px;
}
.foodInfo .rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
}
.foodInfo .rating .ratings_wrapper {
  padding: 0 18px;
}
.foodInfo .rating .ratings_wrapper .ratings_item {
  position: relative;
  padding: 16px 0;
rgba(7,17,27,0.1)
  font-size: 0;
}
.foodInfo .rating .ratings_wrapper .ratings_item .rating_time {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 16px;
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
}
.foodInfo .rating .ratings_wrapper .ratings_item .user {
  display: inline-block;
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.foodInfo .rating .ratings_wrapper .ratings_item .user .name {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
  vertical-align: top;
}
.foodInfo .rating .ratings_wrapper .ratings_item .user .avatar {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  vertical-align: top;
}
.foodInfo .rating .ratings_wrapper .ratings_item .text {
  font-size: 12px;
  line-height: 24px;
  color: #07111b;
  margin-top: 18px;
}
.foodInfo .rating .ratings_wrapper .ratings_item .text .icon {
  margin-right: 4px;
}
.foodInfo .rating .ratings_wrapper .ratings_item .text .icon.icon-thumb_up {
  color: #00a0dc;
}


